import React, { useState } from "react";
import CustomInput from "../components/custom-input.component";
import LoginSignUp from "../components/login-sign-up.component";

const ChangePasswordPage = ({ checkEyes, passwordEye }) => {
  const [formValues, setFormValuse] = useState({
    oldPassword: "",
    password: "",
    confirmPassword: "",
  });

  // console.log(passwordEye);

  const handleChange = (event) => {
    const { name, value } = event.target;

    setFormValuse({ ...formValues, [name]: value });
  };

  return (
    <LoginSignUp pageName="Change Password">
      <form action="">
        <CustomInput
          label="Old Password*"
          type={passwordEye ? "text" : "password"}
          name="oldPassword"
          handlechange={handleChange}
          value={formValues.oldPassword}
          checkEyes={checkEyes}
          passwordEye={passwordEye}
          password
        />

        <CustomInput
          label="Password*"
          type={passwordEye ? "text" : "password"}
          name="password"
          handlechange={handleChange}
          value={formValues.password}
          checkEyes={checkEyes}
          passwordEye={passwordEye}
          password
        />

        <CustomInput
          label="Confirm Password*"
          type={passwordEye ? "text" : "password"}
          name="confirmPassword"
          handlechange={handleChange}
          value={formValues.confirmPassword}
          checkEyes={checkEyes}
          passwordEye={passwordEye}
          password
        />

        <button className="submit-btn">Reset Password</button>
      </form>
    </LoginSignUp>
  );
};

export default ChangePasswordPage;
