import React, { useState, useEffect, useContext } from "react";
import { Link } from "react-router-dom";
import { PlusIcon, SearchIcon } from "../components/icons.component";
import Popup from "../components/popup.component";
import TableComponent from "../components/table.component";
import { OrderContext } from "../context/order.context";
import { url } from "../url";

const PickupRequestPage = ({
  chagesDected,
  setChagesDected,
  setOrderDay,
  searchField,
  setSearchField,
  setPopupMessage,
}) => {
  const [tableAllData] = useContext(OrderContext);

  const [noData, setNoData] = useState("");

  const [popup, setPopup] = useState(false);
  const [yesClicked, setYesClicked] = useState(null);

  const [totalCashCollect, setTotalCashCollect] = useState(null);

  const [selectValue, setSelectValue] = useState(false);
  const [orderId, setOrderId] = useState(null);

  const onSelectChange = (e) => {
    setSelectValue(e.target.value);
    setPopup(true);
  };

  const [tableData, setTableData] = useState({
    tableHead: [
      "S.N",
      "Name",
      "",
      "Contact No.",
      "Address",
      "Cash To Be Collected",
      "Order No.",
      "Action",
    ],

    tableBody: [],
  });

  let filterData = [];

  let totalCash = [];

  useEffect(() => {
    if (tableAllData !== null) {
      tableAllData.forEach((data, idx) => {
        if (data.action === "Hold") {
          let tableBodyData = {
            name: data.customer_name,
            hideId: data.id,
            contactNo: [data.phone_number],
            address: data.address,
            cashToBeCollected: data.product[0].cash_to_be_paid
              ? data.product[0].cash_to_be_paid
              : "",
            orderNo: data.order_number,
            selectAction: null,
          };

          filterData.push(tableBodyData);

          totalCash[0] = 0;

          totalCash[idx + 1] =
            totalCash[idx] + parseInt(data.product[0].cash_to_be_paid);
        }
      });
      setTotalCashCollect(totalCash[totalCash.length - 1]);
    } else {
      setTableData(null);
      setNoData("No Data Found");
    }

    setTableData({ ...tableData, tableBody: filterData });
  }, [tableAllData]);

  useEffect(() => {
    window.scroll(0, 0);
  }, []);

  useEffect(() => {
    if (yesClicked !== null) {
      setPopup(false);
    }
  }, [yesClicked]);

  const orderUpdate = () => {
    var formData = new FormData();
    formData.append("action", selectValue);

    if (selectValue === "Cancel") {
      formData.append("status", "Cancelled");
    }

    fetch(url + `/orders/${orderId}/`, {
      method: "PATCH",
      headers: {
        Accept: "application/json",
        Authorization: "Token " + localStorage.getItem("token"),
      },
      body: formData,
    })
      .then((res) => {
        if (!res.ok) {
          return res.text().then((text) => {
            throw new Error(text);
          });
        }
        return res;
      })
      .then((data) => {
        setPopup(false);
        setChagesDected(chagesDected);
        setPopupMessage("Order has placed!");
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <>
      <Popup title="Are you sure?" popup={popup} setPopup={setPopup} noPadding>
        <div className="confirm-btns">
          <button
            onClick={() => {
              setPopup(false);
              const tableStatusActions =
                document.querySelectorAll("#tableStatusAction");

              tableStatusActions.forEach((tableStatusAction) => {
                tableStatusAction.selectedIndex = 0;
              });
            }}
          >
            No
          </button>
          <button
            onClick={() => {
              setYesClicked(selectValue);
              orderUpdate();
            }}
          >
            Yes
          </button>
        </div>
      </Popup>
      <div className="pickup-request">
        <div className="pick-up-orders">
          <h2 className="title">Pick Up Request</h2>

          <div className="list">
            <div
              className="item"
              style={{ backgroundColor: "rgba(214, 110, 53, 0.16)" }}
              onClick={() => setOrderDay(-1)}
            >
              <h4 className="name">Pick Up Orders for Today</h4>
              <p>30/20</p>
              <Link to="/create-order">
                <button>
                  <PlusIcon /> Add Pickup Request
                </button>
              </Link>
            </div>

            <div
              className="item"
              style={{ backgroundColor: "rgba(210, 214, 53, 0.16)" }}
              onClick={() => setOrderDay(0)}
            >
              <h4 className="name">Pick Up Orders for Tomorrow</h4>
              <p>30/20</p>
              <Link to="/create-order">
                <button>
                  <PlusIcon /> Add Pickup Request
                </button>
              </Link>
            </div>

            <div
              className="item"
              style={{ backgroundColor: "rgba(56, 214, 53, 0.16)" }}
              onClick={() => setOrderDay(1)}
            >
              <h4 className="name">
                Pick Up Orders for The Day After Tommorrow
              </h4>
              <p>30/20</p>
              <Link to="/create-order">
                <button>
                  <PlusIcon /> Add Pickup Request
                </button>
              </Link>
            </div>

            <div
              className="item"
              style={{ backgroundColor: "rgba(53, 214, 194, 0.16)" }}
              onClick={() => setOrderDay(2)}
            >
              <h4 className="name">No. of Pick Up Orders on Hold</h4>
              <p>30/20</p>
              <Link to="/create-order">
                <button>
                  <PlusIcon /> Add Pickup Request
                </button>
              </Link>
            </div>

            <div
              className="item"
              style={{ backgroundColor: "rgba(214, 53, 53, 0.16)" }}
            >
              <h4 className="name">No. of Pick Up Orders on Hold</h4>
              <p>30/20</p>
              <Link to="/create-order">
                <button>
                  <PlusIcon /> Add Emergency Pickup
                </button>
              </Link>
            </div>
          </div>
        </div>

        <div className="table">
          <div className="title-section">
            <h2 className="title">Pick Up Orders</h2>

            <div className={`search ${searchField !== "" ? "active" : ""}`}>
              <label htmlFor="searchField" className="icon">
                <SearchIcon />
              </label>

              <input
                type="search"
                placeholder="Search..."
                id="searchField"
                name="searchField"
                onChange={(e) => setSearchField(e.target.value)}
              />
            </div>
          </div>

          {tableData.tableBody[0] !== undefined ? (
            <TableComponent
              setPopup={setPopup}
              tableDatas={tableData}
              searchField={searchField}
              yesClicked={yesClicked}
              setOrderId={setOrderId}
              onSelectChange={onSelectChange}
              totalCashCollect={totalCashCollect}
              noData={noData}
            />
          ) : (
            "No Data Found"
          )}
        </div>
      </div>
    </>
  );
};

export default PickupRequestPage;
