import React, { useContext, useState, useEffect, useRef } from "react";
import { ChatContext } from "../context/chat.context";
import userImage from "../assets/images/users/profile2.jpeg";
import CustomInput from "./custom-input.component";
import { CloseIcon } from "./icons.component";
import { mainurl } from "../url";
import { w3cwebsocket as W3CWebSocket } from "websocket";
import { webAddress } from "../url";

const LetterPopup = ({ letterPopup, setLetterPopup, chatData }) => {
  const [message, setMessage] = useState("");

  const [letter, setLetter] = useState("");

  const [getClient, setGetClient] = useState(null);

  useEffect(() => {
    if (letterPopup) {
      const client = new W3CWebSocket(
        `ws://${webAddress}/ws/chat/${chatData.id}/`
      );
      setGetClient(client);

      client.onopen = () => {
        console.log("WebSocket Client Connected");
      };
      client.onmessage = (message) => {
        console.log("reply from server");
        const data = JSON.parse(message.data);

        if (data) {
          setLetter(data);
          scroll.current.scrollIntoView({ behavior: "smooth" });
        }
      };
    }

    return;
  }, [letterPopup]);

  const scroll = useRef();

  const user = parseInt(localStorage.getItem("id"));

  const onSendClicked = (e) => {
    e.preventDefault();

    getClient &&
      getClient.send(
        JSON.stringify({
          type: "message",
          message: `${message}`,
          user_id: user,
        })
      );

    setMessage("");
  };

  const handleChange = (event) => {
    setMessage(event.target.value);
  };

  const sendMessage = (e) => {
    e.preventDefault();
    console.log(message);
  };

  return (
    <div className={`letter-popup ${letterPopup ? "active" : ""}`}>
      <div className="letter-head">
        <div className="image">
          <img
            src={`${mainurl}/media/${chatData.online[0].profile_picture}`}
            alt="userImage"
          />

          <div className="others">
            <div className="name">{chatData.online[0].full_name}</div>
            <div className="status">Active</div>
          </div>
        </div>

        <button
          className="letter-popup-close"
          onClick={() => {
            setLetterPopup(false);
            getClient.close();
          }}
        >
          <CloseIcon />
        </button>
      </div>

      <div className="letter-body">
        <div className="inner">
          {letter &&
            letter.message.map((data, idx) => {
              if (data.user === user) {
                return (
                  <div className="your-letter" key={idx}>
                    <div className="letter-box">{data.content}</div>
                  </div>
                );
              } else {
                return (
                  <div className="other-letter" key={idx}>
                    <img
                      src={`${mainurl}/media/${chatData.online[0].profile_picture}`}
                      alt=""
                    />

                    <div className="letter-box">{data.content}</div>
                  </div>
                );
              }
            })}
        </div>
        <div ref={scroll}></div>
      </div>

      <div className="letter-footer">
        <form action="">
          <div className="letter-input">
            <input
              type="text"
              placeholder="Type your letter here..."
              name="message"
              onChange={handleChange}
              value={message}
            />
            <button
              onClick={(e) => {
                onSendClicked(e);
              }}
            >
              Send
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default LetterPopup;
