import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";
import CustomInput from "../components/custom-input.component";
import { UploadIcon } from "../components/icons.component";
import { url, mainurl } from "../url";
import useFetch from "../useFetch";

const UpdateOrderPage = ({ setPopupMessage }) => {
  const [formValues, setFormValuse] = useState({
    pickupDate: "",
    customer: "",
    cashPaid: "",
    address: "",
    nextPersonName: "",
    nextPersonNumber: "",
    cashToBePaid: "",
    description: "",
    category: "",
    image: "",
    weight: "",
    status: "",
    nearestLandmark: "",
    mobileNumber: "",
    extraMobileNumber: "",
    pickUpAddress: "",
  });
  const { id } = useParams();
  const { data: orderDetail } = useFetch(`${url}/orders/${id}`);

  useEffect(() => {
    if (orderDetail) {
      setFormValuse((previousInputs) => ({
        ...previousInputs,
        status: orderDetail["status"] || "",
      }));
      setFormValuse((previousInputs) => ({
        ...previousInputs,
        cashPaid: orderDetail["cash_paid"] || "",
      }));
      setFormValuse((previousInputs) => ({
        ...previousInputs,
        customer: orderDetail["customer_name"] || "",
      }));
      setFormValuse((previousInputs) => ({
        ...previousInputs,
        pickupDate: orderDetail["pick_up_date"] || "",
      }));
      setFormValuse((previousInputs) => ({
        ...previousInputs,
        address: orderDetail["address"] || "",
      }));
      setFormValuse((previousInputs) => ({
        ...previousInputs,
        pickUpAddress: orderDetail["pick_up_address"] || "",
      }));
      if (orderDetail["nearest_landmark"]) {
        setFormValuse((previousInputs) => ({
          ...previousInputs,
          nearestLandmark: orderDetail["nearest_landmark"] || "",
        }));
      }
      setFormValuse((previousInputs) => ({
        ...previousInputs,
        mobileNumber: `${orderDetail["phone_number"] || ""}`,
      }));
      setFormValuse((previousInputs) => ({
        ...previousInputs,
        cashToBePaid: orderDetail["product"]["cash_to_be_paid"] || "",
      }));
      setFormValuse((previousInputs) => ({
        ...previousInputs,
        description: orderDetail["product"]["description"],
      }));
      if (orderDetail["telephone_number"]) {
        setFormValuse((previousInputs) => ({
          ...previousInputs,
          extraMobileNumber: orderDetail["telephone_number"],
        }));
      }
      if (orderDetail["product"]["image"]) {
        document.querySelectorAll(
          ".image-div"
        )[0].innerHTML = `<img src="${mainurl}${orderDetail["product"]["image"]}" alt="">`;
      }
      // console.log(`${mainurl}${orderDetail['product']['image']}`)
    }
  }, [orderDetail]);

  const [image, setImage] = useState(null);
  const [wrongData, setWrongData] = useState(null);
  const [done, setDone] = useState(false);

  const [errorCustomerName, setErrorCustomerName] = useState(null);
  const [errorNextPersonNumber, setErrorNextPersonNumber] = useState(null);
  const [errorPickUpDate, setErrorPickUpDate] = useState(null);
  const [errorWeight, setErrorWeight] = useState(null);

  const history = useHistory();

  const handleChange = (event) => {
    const { name, value } = event.target;

    setFormValuse({ ...formValues, [name]: value });
  };

  const [errorPickupDate, setErrorPickupDate] = useState(null);

  const [errorMessage, setErrorMessage] = useState([null]);

  const validate = (e) => {
    e.preventDefault();

    let test = {
      description: formValues.description,
      cash_to_be_paid: formValues.cashToBePaid,
      // category: formValues.category,
      // weight: formValues.weight,
    };

    var formData = new FormData();
    formData.append("pick_up_date", formValues.pickupDate);
    // formData.append("next_person_number", formValues.nextPersonNumber);
    // formData.append("next_person_name", formValues.nextPersonName);
    formData.append("cash_paid", formValues.cashPaid);
    formData.append("customer_name", formValues.customer);
    formData.append("address", formValues.address);
    formData.append("nearest_landmark", formValues.nearestLandmark);
    formData.append("pick_up_date", formValues.pickupDate);
    formData.append("pick_up_address", formValues.pickUpAddress);
    formData.append("status", formValues.status);
    formData.append("phone_number", formValues.mobileNumber);
    formData.append("telephone_number", formValues.extraMobileNumber);
    formData.append("product", JSON.stringify(test));
    if (image) {
      formData.append("image", image, image.name);
    }

    // if (!done) {
    //   return 0;
    // }

    fetch(url + `/orders/${id}/`, {
      method: "PUT",
      headers: {
        Accept: "application/json",
        Authorization: "Token " + localStorage.getItem("token"),
        // 'Content-Type':'multipart/form-data'
      },
      body: formData,
    })
      .then((res) => {
        if (!res.ok) {
          return res.text().then((text) => {
            throw new Error(text);
          });
        }
        return res.json();
      })
      .then((data) => {
        setWrongData(false);
        history.push({
          pathname: "/orders",
        });

        setPopupMessage("Updated Sucessed!!!");
      })
      .catch((err) => {
        const errors = JSON.parse(err.message);

        console.log(errors);

        if (errors.customer_name !== undefined) {
          const userError = errors.customer_name[0];

          setErrorCustomerName(userError);
          setErrorMessage(userError);
        }

        if (errors.next_person_number !== undefined) {
          const userError = errors.next_person_number[0];

          setErrorNextPersonNumber(userError);
          setErrorMessage(userError);
        }

        if (errors.pick_up_date !== undefined) {
          const userError = errors.pick_up_date[0];

          setErrorPickUpDate("Formate should be YYYY-MM-DD");
          setErrorMessage("Formate should be YYYY-MM-DD");
        }

        if (errors.product.weight !== undefined) {
          const userError = errors.product.weight[0];

          setErrorWeight(userError);
          setErrorMessage(userError);
        }

        setWrongData(true);
      });
  };

  useEffect(() => {
    window.scroll(0, 0);

    const imageUploadBtn = document.querySelectorAll("#imageUploadBtn");
    const imageDiv = document.querySelectorAll(".image-div");
    const imageUploadInput = document.querySelectorAll("#imageUploadInput");
    imageUploadBtn.forEach((btn, idx) => {
      btn.addEventListener("click", (e) => {
        e.preventDefault();
        imageUploadInput[idx].click();
      });

      imageUploadInput[idx].addEventListener("change", (e) => {
        let file = e.target.files[0];
        setImage(e.target.files[0]);

        btn.classList.add("active");

        let fileReader = new FileReader();

        fileReader.onload = () => {
          let fileUrl = fileReader.result;

          imageDiv[idx].innerHTML = `<img src="${fileUrl}" alt="">`;
        };

        fileReader.readAsDataURL(file);
      });
    });
  }, []);

  return (
    <div className="profile-page">
      <h2 className="title">Update Order</h2>

      <div className="inner-section">
        {wrongData ? (
          <p style={{ color: "red", marginBottom: 10 }}>{errorMessage}</p>
        ) : null}
        <form action="" onSubmit={validate}>
          <div className="basic-informations">
            <h3 className="sec-title">Pickup Date</h3>

            <div className="inner-details">
              <div className="input-groups">
                <CustomInput
                  label="Pickup Date*"
                  type="text"
                  name="pickupDate"
                  placeholder="YYYY-MM-DD"
                  handlechange={handleChange}
                  value={formValues.pickupDate}
                  errorMessage={errorPickUpDate ? errorPickUpDate : null}
                  required
                />
                <div className="group">
                  <select
                    name="status"
                    onChange={handleChange}
                    value={formValues.status}
                    required
                  >
                    <option value="" disabled>
                      Status*
                    </option>
                    <option value="Pending">Pending</option>
                    <option value="Delivered">Delivered</option>
                    <option value="Delivered with Adjustments">
                      Delivered with Adjustments
                    </option>
                    <option value="Cancelled">Cancelled</option>
                    <option value="Problematic">Problematic</option>
                  </select>
                </div>
              </div>
            </div>
          </div>

          <div className="basic-informations">
            <h3 className="sec-title">Product Informations</h3>

            <div className="inner-details">
              <div className="input-groups">
                <CustomInput
                  label="Customer*"
                  type="text"
                  name="customer"
                  handlechange={handleChange}
                  value={formValues.customer}
                  errorMessage={errorCustomerName ? errorCustomerName : null}
                  required
                />

                <CustomInput
                  label="Cash Paid"
                  type="number"
                  name="cashPaid"
                  handlechange={handleChange}
                  value={formValues.cashPaid}
                />
                <CustomInput
                  label="Cash to be Paid"
                  type="number"
                  name="cashToBePaid"
                  handlechange={handleChange}
                  value={formValues.cashToBePaid}
                />

                <CustomInput
                  label="Address*"
                  type="text"
                  name="address"
                  handlechange={handleChange}
                  value={formValues.address}
                  required
                />
                <CustomInput
                  label="Nearest Landmark"
                  type="text"
                  name="nearestLandmark"
                  handlechange={handleChange}
                  value={formValues.nearestLandmark}
                />
                <CustomInput
                  label="Pick Up Address"
                  type="text"
                  name="pickUpAddress"
                  handlechange={handleChange}
                  value={formValues.pickUpAddress}
                />
                <CustomInput
                  label="Mobile Number"
                  type="number"
                  name="mobileNumber"
                  handlechange={handleChange}
                  value={formValues.mobileNumber}
                />

                <CustomInput
                  label="Telephone Number"
                  type="number"
                  name="extraMobileNumber"
                  handlechange={handleChange}
                  value={formValues.extraMobileNumber}
                />
                <CustomInput
                  label="Description"
                  type="text"
                  name="description"
                  handlechange={handleChange}
                  value={formValues.description}
                />
              </div>
            </div>
          </div>

          <div className="teams-sections">
            <div className="owner">
              <div className="sec-title">Product Image</div>

              <div className="inner-details">
                <div className="image-upload">
                  <div className="image-div"></div>

                  <input
                    type="file"
                    name="image"
                    id="imageUploadInput"
                    onChange={handleChange}
                    value={formValues.image}
                    className="active"
                    hidden
                  />

                  <button id="imageUploadBtn">
                    <UploadIcon />
                    <span>Click To upload Image</span>
                  </button>
                </div>
              </div>
            </div>
          </div>
          {/* 
          <div className="basic-informations">
            <h3 className="sec-title">Shipping Informations</h3>

            <div className="inner-details">
              <div className="input-groups">
                <CustomInput
                  label="Next Person Name*"
                  type="text"
                  name="nextPersonName"
                  handlechange={handleChange}
                  value={formValues.nextPersonName}
                  required
                />

                <CustomInput
                  label="Next Person Number*"
                  type="number"
                  name="nextPersonNumber"
                  handlechange={handleChange}
                  value={formValues.nextPersonNumber}
                  errorMessage={
                    errorNextPersonNumber ? errorNextPersonNumber : null
                  }
                  required
                />

                <CustomInput
                  label="Cash To Be Paid*"
                  type="number"
                  name="cashToBePaid"
                  handlechange={handleChange}
                  value={formValues.cashToBePaid}
                  required
                />

                <CustomInput
                  label="Description*"
                  type="text"
                  name="description"
                  handlechange={handleChange}
                  value={formValues.description}
                  required
                />
                <div className="group">
                  <select
                    name="category"
                    onChange={handleChange}
                    value={formValues.category}
                    required
                  >
                    <option value="" disabled>
                      Category*
                    </option>
                    {productCategory &&
                      productCategory.map((cat, id) => {
                        return (
                          <option value={cat.id} key={cat.id}>
                            {cat.category_name}
                          </option>
                        );
                      })}
                  </select>
                </div>

                <CustomInput
                  label="Weight*"
                  type="number"
                  name="weight"
                  handlechange={handleChange}
                  value={formValues.weight}
                  errorMessage={errorWeight ? errorWeight : null}
                  required
                />
              </div>
            </div>
          </div> */}

          <button type="submit" className="submit">
            Update
          </button>
        </form>
      </div>
    </div>
  );
};

export default UpdateOrderPage;
