import React from "react";
import { Link } from "react-router-dom";
import { DeleteIcon, EditIcon } from "./icons.component";

const TableComponent = ({
  tableDatas,
  color,
  setPopup,
  pageLink,
  setOrderId,
  total,
  onSelectChange,
  searchField,
  totalCashCollect,
  noData,
}) => {
  return (
    <>
      {!noData ? (
        <div className="table">
          <table cellSpacing="0" cellPadding="0">
            <thead>
              <tr style={{ backgroundColor: `${color}` }}>
                {tableDatas.tableHead.map((data, idx) => {
                  return <th key={idx}>{data}</th>;
                })}
              </tr>
            </thead>
            <tbody>
              {tableDatas.tableBody.map((data, idx) => {
                let keys = Object.keys(data);

                return (
                  <tr key={idx}>
                    <td>{idx + 1}</td>
                    {keys.map((key, index) => (
                      <td key={index + 2}>
                        {key === "status" ? (
                          <h4 className={`${data[key]}`}>{data[key]}</h4>
                        ) : key === "contactNo" ? (
                          data.contactNo.map((contact, idx) => (
                            <div className="contactNo" key={idx}>
                              {contact}
                            </div>
                          ))
                        ) : key === "remarks" ? (
                          <div className="remarks" style={{ width: "200px" }}>
                            {data[key].length}...
                          </div>
                        ) : key === "hideId" ? (
                          <div style={{ display: "none", position: "fixed" }}>
                            {data[key]}
                          </div>
                        ) : data[key] !== null ? (
                          data[key]
                        ) : (
                          ""
                        )}

                        {key === "action" ? (
                          <div className="button-group">
                            <Link to={`${pageLink}/${data["hideId"]}`}>
                              <button>
                                <EditIcon />
                              </button>
                            </Link>

                            {/*
                            <button
                              className="delete-btn"
                              onClick={() => {
                                setPopup(true);
                                setOrderId(data["orderNo"]);
                              }}
                            >
                              <DeleteIcon />
                            </button>
                            */}
                          </div>
                        ) : null}

                        {key === "selectAction" ? (
                          <select
                            name=""
                            id="tableStatusAction"
                            onChange={(e) => {
                              onSelectChange(e);
                              setOrderId(data["hideId"]);
                            }}
                          >
                            <option value="">--Select--</option>
                            <option value="Hold">Hold</option>
                            <option value="Request">Request</option>
                            <option value="Cancel">Cancel</option>
                          </select>
                        ) : null}
                      </td>
                    ))}
                  </tr>
                );
              })}

              {totalCashCollect ? (
                <tr className={totalCashCollect ? "total" : ""}>
                  <td></td>
                  <td>Total</td>

                  {tableDatas.tableHead
                    .filter(
                      (data, idx) => idx < tableDatas.tableHead.length - 3
                    )
                    .map((data, idx) => (
                      <td key={idx}></td>
                    ))}
                  <td>Rs {totalCashCollect}/-</td>
                </tr>
              ) : null}
            </tbody>
          </table>
        </div>
      ) : (
        noData
      )}
    </>
  );
};

export default TableComponent;
