import React, { useState, useEffect } from "react";
import CustomInput from "../components/custom-input.component";
import {
  CallIcon,
  EnvelopeIcon,
  FacebookIcon,
  InstagramIcon,
  LinkedinIcon,
  MapMarker,
  TwitterIcon,
} from "../components/icons.component";

const ContactPage = () => {
  const [formValues, setFormValuse] = useState({
    fullName: "",
    contactNo: "",
    email: "",
    subject: "",
    message: "",
  });

  const handleChange = (event) => {
    const { name, value } = event.target;

    setFormValuse({ ...formValues, [name]: value });
  };

  useEffect(() => {
    window.scroll(0, 0);
  }, []);

  const Iframe = ({ src, height, width }) => {
    return (
      <div>
        <iframe src={src} height={height} width={width} title="Google Map" />
      </div>
    );
  };

  return (
    <div className="contact-page">
      <div className="contact-form">
        <h2 className="title">Contact Us</h2>

        <form action="">
          <CustomInput
            label="Full Name*"
            type="text"
            name="fullName"
            handlechange={handleChange}
            value={formValues.fullName}
          />

          <CustomInput
            label="Contact No*"
            type="number"
            name="contactNo"
            handlechange={handleChange}
            value={formValues.contactNo}
          />

          <CustomInput
            label="Email*"
            type="email"
            name="email"
            handlechange={handleChange}
            value={formValues.email}
          />

          <CustomInput
            label="Subject*"
            type="text"
            name="subject"
            handlechange={handleChange}
            value={formValues.subject}
          />

          <CustomInput
            label="Message*"
            name="message"
            handlechange={handleChange}
            value={formValues.message}
            textArea
          />

          <button type="submit">Submit</button>
        </form>
      </div>

      <div className="other-sec">
        <div className="map">
          <h2 className="title">Our Location</h2>
          <Iframe
            src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3532.8640191287623!2d85.3263988149522!3d27.690597382798867!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x39eb19bb84ec646d%3A0xf514a3a9c2e133f7!2sDeepMind%20Creations%20Pvt%20Ltd!5e0!3m2!1sen!2snp!4v1652334157956!5m2!1sen!2snp"
            width="100%"
            height="250"
            allowfullscreen=""
            loading="lazy"
            referrerpolicy="no-referrer-when-downgrade"
          ></Iframe>
        </div>

        <div className="contact-informations">
          <h2 className="title">Our Location</h2>
          <ul>
            <li>
              <div className="icon">
                <CallIcon />
              </div>
              <strong>Call: </strong>
              +977-9876543210
            </li>

            <li>
              <div className="icon">
                <EnvelopeIcon />
              </div>
              <strong>Email: </strong>
              info@gmail.com
            </li>

            <li>
              <div className="icon">
                <MapMarker />
              </div>
              <strong>Location: </strong>
              Tinkune, Kathmandu, Bagmati
            </li>
          </ul>
        </div>

        <div className="contact-informations">
          <h2 className="title"> Social Links</h2>
          <ul>
            <li>
              <button>
                <div className="icon">
                  <FacebookIcon />
                </div>
                <strong>Facebook </strong>
              </button>
            </li>

            <li>
              <button>
                <div className="icon">
                  <TwitterIcon />
                </div>
                <strong>Twitter </strong>
              </button>
            </li>

            <li>
              <button>
                <div className="icon">
                  <InstagramIcon />
                </div>
                <strong>Instagram </strong>
              </button>
            </li>

            <li>
              <button>
                <div className="icon">
                  <LinkedinIcon />
                </div>
                <strong>Linked In </strong>
              </button>
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default ContactPage;
