import React from "react";
import { CloseIcon } from "./icons.component";

const Popup = ({ children, title, popup, setPopup, noPadding }) => {
  return (
    <>
      <div
        className={`popup-bg ${popup ? "active" : ""} `}
        onClick={() => setPopup(false)}
      ></div>

      <div
        className={`popup ${popup ? "active" : ""} ${
          noPadding ? "no-padding" : ""
        }`}
      >
        <button className="close-btn" onClick={() => setPopup(false)}>
          <CloseIcon />
        </button>

        <div className="title">{title}</div>

        <div className="popup-item">{children}</div>
      </div>
    </>
  );
};

export default Popup;
