import React, { useState, useEffect } from "react";
import { Link, useHistory, useLocation } from "react-router-dom";

import CustomInput from "../../components/custom-input.component";
import LoginSignUp from "../../components/login-sign-up.component";
import { url } from "../../url";

const ForgetPasswordPage = () => {
  const [formValues, setFormValuse] = useState({
    password: "",
    confirmPassword: "",
  });
  const location = useLocation();
  let email;
  try {
    email = location.state.email;
  } catch (err) {
    email = null;
  }
  console.log(email);
  const history = useHistory();
  const [wrongData, setWrongData] = useState(null);
  const [isMatched, setIsMatched] = useState("empty");

  const handleChange = (event) => {
    const { name, value } = event.target;

    setFormValuse({ ...formValues, [name]: value });
  };

  useEffect(() => {
    if (formValues.password === "" || formValues.confirmPassword === "") {
      setIsMatched("empty");
    }
    if (isMatched === "exist") {
      return 0;
    }
    if (
      formValues.password.length < 8 ||
      formValues.confirmPassword.length < 8
    ) {
      setIsMatched("short");
    } else if (
      formValues.password === formValues.confirmPassword &&
      formValues.password.length >= 8 &&
      formValues.confirmPassword.length >= 8
    ) {
      setIsMatched("done");
    } else {
      setIsMatched("mistake");
    }
  }, [formValues, isMatched]);

  const changePassword = (e) => {
    e.preventDefault();
    fetch(url + "/reset_password/", {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
      },

      body: JSON.stringify({
        password: formValues.password,
        confirm_password: formValues.confirmPassword,
        email: email,
      }),
    })
      .then((res) => {
        if (!res.ok) {
          return res.text().then((text) => {
            throw new Error(text);
          });
        }
        return res.json();
      })
      .then((data) => {
        history.push({
          pathname: "/login",
        });
      })
      .catch((err) => setWrongData(true));
  };

  return (
    <LoginSignUp pageName="Forget Password">
      {wrongData && <p>Something went wrong.</p>}

      <form action="" onSubmit={changePassword}>
        <CustomInput
          label="Password*"
          type="password"
          name="password"
          handleChange={handleChange}
          value={formValues.password}
          password
          required
        />

        <CustomInput
          label="Confirm Password*"
          type="password"
          name="confirmPassword"
          handleChange={handleChange}
          value={formValues.confirmPassword}
          password
          required
        />
        {isMatched === "empty" ? (
          <p
            style={{
              color: "black",
              marginBottom: 6,
            }}
          >
            *Password must be atleast 8 characters!
          </p>
        ) : isMatched === "mistake" ? (
          <p
            style={{
              color: "red",
              marginBottom: 6,
            }}
          >
            Password didn't Match!
          </p>
        ) : isMatched === "short" ? (
          <p
            style={{
              color: "black",
              marginBottom: 6,
            }}
          >
            *Password must be atleast 8 characters!
          </p>
        ) : isMatched === "exist" ? (
          <p
            style={{
              color: "red",
              marginBottom: 6,
            }}
          >
            Account from this phone number already exists.
          </p>
        ) : (
          <p style={{ color: "green", marginBottom: 6 }}>Password Matched!</p>
        )}
        <button
          className="submit-btn"
          disabled={isMatched === "done" ? false : true}
        >
          Reset Password
        </button>
      </form>

      <div className="switch-login-signup">
        New To Sanam Delivery? <Link to="/email-verification">Sign Up</Link>
      </div>
    </LoginSignUp>
  );
};

export default ForgetPasswordPage;
