import React, { useEffect } from "react";
import { Link, useHistory } from "react-router-dom";
import {
  BoxIcon,
  CloseIcon,
  ContactIcon,
  DashboardIcon,
  ListIcon,
  PickUpIcon,
  UserIcon,
} from "./icons.component";
import logo from "../assets/images/logo/final-logo-white.png";

const SideNav = ({ activeSideNav, hideSideNav, showSideNav }) => {
  let history = useHistory();

  useEffect(() => {
    const navLinks = document.querySelectorAll(".nav .link a");
    const link = document.querySelectorAll(".nav .link");

    navLinks.forEach((navLink, idx) => {
      let linkUrl = navLink.getAttribute("href");

      if (linkUrl === history.location.pathname) {
        link[idx].classList.add("active");
      } else {
        link[idx].classList.remove("active");
      }
    });
  });

  return (
    <div
      className={`side-nav ${activeSideNav ? "active" : ""} ${
        hideSideNav ? "show" : ""
      }`}
    >
      <div className="top-sec">
        <Link to="/">
          <div className="logo">
            <img src={logo} alt="logo" />
          </div>
        </Link>

        <div
          className="side-nav-close"
          onClick={() => showSideNav(hideSideNav)}
        >
          <CloseIcon />
        </div>
      </div>

      <ul className="nav">
        <li className="link active">
          <Link to="/">
            <button onClick={() => showSideNav(hideSideNav)}>
              <DashboardIcon />
              <span>Dashboard</span>
            </button>
          </Link>
        </li>

        <li className="link">
          <Link to="/orders">
            <button onClick={() => showSideNav(hideSideNav)}>
              <BoxIcon />
              <span>Orders</span>
            </button>
          </Link>
        </li>

        <li className="link">
          <Link to="/pick-up-request">
            <button onClick={() => showSideNav(hideSideNav)}>
              <PickUpIcon />
              <span>Pick Up Request</span>
            </button>
          </Link>
        </li>

        <li className="link">
          <Link to="/accounts">
            <button onClick={() => showSideNav(hideSideNav)}>
              <UserIcon />
              <span>Account Ledger</span>
            </button>
          </Link>
        </li>

        <li className="link">
          <Link to="/reports">
            <button onClick={() => showSideNav(hideSideNav)}>
              <ListIcon />
              <span>Reports</span>
            </button>
          </Link>
        </li>

        <li className="link">
          <Link to="/contact">
            <button onClick={() => showSideNav(hideSideNav)}>
              <ContactIcon />
              <span>Contact</span>
            </button>
          </Link>
        </li>
      </ul>
    </div>
  );
};

export default SideNav;
