import React, { useState } from "react";
import { Link } from "react-router-dom";
import { useHistory } from "react-router";

import {
  ArrowIcon,
  NotificationIcon,
  SearchIcon,
  LogoutIcon,
  PasswordIcon,
  UserIcon,
  CloseIcon,
  MessageIcon,
  ChatIcon,
} from "./icons.component";
import userImage from "../assets/images/users/images.png";
import { mainurl } from "../url";

const Header = ({
  activeSideNav,
  activateSideNav,
  hideSideNav,
  showSideNav,
  profile,
}) => {
  const history = useHistory();
  const [searchPopUp, setSearchPopup] = useState(false);
  const [formValues, setFormValuse] = useState({
    searchField: "",
    searchFieldMobile: "",
  });

  const handleChange = (event) => {
    const { name, value } = event.target;

    setFormValuse({ ...formValues, [name]: value });
  };

  const logout = (e) => {
    localStorage.removeItem("token");
    history.push("/login");
    window.location.reload();
  };

  return (
    <>
      <div className={`search-popup ${searchPopUp ? "active" : ""}`}>
        <div
          className="search-popup-close"
          onClick={() => setSearchPopup(false)}
        >
          <CloseIcon />
        </div>

        <div className="search-bg"></div>

        <form action="">
          <div className="search-field">
            <input
              type="text"
              id="searchFieldMob"
              placeholder="Search..."
              name="searchFieldMobile"
              onChange={handleChange}
              value={formValues.searchFieldMobile}
            />
            <div
              className={`line ${
                formValues.searchFieldMobile.length ? "active" : ""
              }`}
            ></div>
            <button
              className={` ${
                formValues.searchFieldMobile.length ? "active" : ""
              }`}
            >
              <SearchIcon />
            </button>
          </div>
        </form>
      </div>
      <header>
        <div className="inner">
          <div className="side-nav-button">
            <button
              className="side-nav-btn"
              onClick={() => {
                activateSideNav(activeSideNav);
              }}
            >
              <div className="bars">
                <div className="bar"></div>
              </div>

              <div className={`arrow ${activeSideNav ? "rotate" : ""}`}>
                <ArrowIcon />
              </div>
            </button>

            <button
              className="side-nav-btn open-close"
              onClick={() => {
                showSideNav(hideSideNav);
              }}
            >
              <div className="bars">
                <div className="bar"></div>
              </div>

              <div className={`arrow ${activeSideNav ? "rotate" : ""}`}>
                <ArrowIcon />
              </div>
            </button>
          </div>

          <div className="others">
            <form action="" id="headerSearch">
              <div
                className={`search ${
                  formValues.searchField !== "" ? "active" : ""
                }`}
              >
                <label htmlFor="searchField" className="icon">
                  <SearchIcon />
                </label>

                <input
                  type="text"
                  placeholder="Search..."
                  id="searchField"
                  name="searchField"
                  onChange={handleChange}
                  value={formValues.searchField}
                />

                <button className="search-btn">
                  <SearchIcon />
                </button>
              </div>
            </form>

            <div
              className="useful-button"
              id="searchPopupBtn"
              onClick={() => setSearchPopup(true)}
            >
              <button>
                <SearchIcon />
              </button>
            </div>

            <div className="useful-button">
              <Link to="/notifications">
                <button>
                  <NotificationIcon />
                </button>
              </Link>
            </div>

            <div className="useful-button">
              <Link to="/chat">
                <button>
                  <ChatIcon />
                </button>
              </Link>
            </div>

            <div className="profile-button">
              <img src={profile ? `${profile}` : userImage} alt="user" />

              <div className="profile-option-popup">
                <ul>
                  <li>
                    <Link to="/profile">
                      <button>
                        <UserIcon /> Profile
                      </button>
                    </Link>
                  </li>

                  <li>
                    <Link to="/change-password">
                      <button>
                        <PasswordIcon /> Change Password
                      </button>
                    </Link>
                  </li>

                  <li>
                    <Link to="">
                      <button onClick={logout}>
                        <LogoutIcon /> Logout
                      </button>
                    </Link>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </header>
    </>
  );
};

export default Header;
