import React, { useContext, useEffect, useState } from "react";
import { SearchIcon } from "../components/icons.component";
import Popup from "../components/popup.component";
import TableComponent from "../components/table.component";
import { OrderContext } from "../context/order.context";
import { url } from "../url";

const OrderPage = ({
  chagesDected,
  setChagesDected,
  searchField,
  setSearchField,
  setPopupMessage,
}) => {
  const [tableAllData] = useContext(OrderContext);

  const [orderId, setOrderId] = useState(null);
  const [popup, setPopup] = useState(null);

  const [totalCashCollect, setTotalCashCollect] = useState(null);

  const [tableData, setTableData] = useState({
    tableHead: [
      "S.N",
      "Today's Orders",
      "",
      "Order No.",
      "Cash Collection",
      "Delevery Charge",
      "Status",
      "Action",
    ],

    tableBody: [],
  });

  let filterData = [];

  let totalCash = [];

  useEffect(() => {
    if (tableAllData !== null) {
      tableAllData.forEach((data, idx) => {
        if (
          (data.action === "Request" && data.approve === true) ||
          (data.action === "Cancel" && data.approve === true)
        ) {
          let tableBodyData = {
            name: data.customer_name,
            hideId: data.id,
            orderNo: data.order_number,
            cashToBeCollected: data.product[0].cash_to_be_paid,
            deliveryCharge: data.delivery_charge,
            status: data.status,
            action: null,
          };

          filterData.push(tableBodyData);
        }

        totalCash[0] = 0;

        totalCash[idx + 1] =
          totalCash[idx] + parseInt(data.product[0].cash_to_be_paid);
      });

      setTotalCashCollect(totalCash[totalCash.length - 1]);
    } else {
      setTableData(null);
    }

    setTableData({ ...tableData, tableBody: filterData });
  }, [tableAllData]);

  const orderDelete = () => {
    fetch(url + `/orders/${orderId}/`, {
      method: "DELETE",
      headers: {
        Accept: "application/json",
        Authorization: "Token " + localStorage.getItem("token"),
      },
    })
      .then((res) => {
        if (!res.ok) {
          return res.text().then((text) => {
            throw new Error(text);
          });
        }
        return res;
      })
      .then((data) => {
        setChagesDected(chagesDected);
        setPopupMessage("Delete Sucessed!!!");
        setPopup(false);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <div className="order-page">
      <div className="title-section">
        <h2 className="title">Pick Up Orders</h2>

        <div className="search-area">
          <div className={`search ${searchField !== "" ? "active" : ""}`}>
            <label htmlFor="filterData" className="icon">
              <SearchIcon />
            </label>

            <input
              type="search"
              placeholder="Search..."
              id="filterData"
              name="searchField"
              onChange={(e) => setSearchField(e.target.value)}
            />
          </div>
        </div>
      </div>

      <Popup title="Are you sure?" popup={popup} setPopup={setPopup} noPadding>
        <div className="confirm-delete-btns">
          <button onClick={() => setPopup(false)}>Cancel</button>
          <button onClick={orderDelete}>Delete</button>
        </div>
      </Popup>

      <div className="table">
        {tableData.tableBody[0] !== undefined ? (
          <TableComponent
            setPopup={setPopup}
            pageLink="/update-order"
            setOrderId={setOrderId}
            tableDatas={tableData}
            searchField={searchField}
            totalCashCollect={totalCashCollect}
          />
        ) : (
          "No Data Found"
        )}
      </div>
    </div>
  );
};

export default OrderPage;
