import React, { createContext, useEffect, useState } from "react";
import Loading from "../components/loading.component";
import { mainurl } from "../url";
import useFetch from "../useFetch";

export const ChatContext = createContext();

export const ChatProvider = ({ children }) => {
  const [chatList, setChatList] = useState([
    {
      id: 1,
      userName: "Naruto",
      status: true,
      link: "/letter",
      messages: {
        yourLetter: ["Hello", "How are you?"],
        otherLetter: ["Hi", "I am fine. How are you?"],
      },
      imageURL:
        "https://images.pexels.com/photos/5821482/pexels-photo-5821482.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260",
    },

    {
      id: 2,
      userName: "Sasuke",
      status: false,
      link: "/letter",
      messages: {
        yourLetter: ["Hello", "How are you?"],
        otherLetter: ["Hi", "I am fine. How are you?"],
      },
      imageURL:
        "https://images.pexels.com/photos/1310522/pexels-photo-1310522.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2",
    },

    {
      id: 3,
      userName: "Itachi",
      status: true,
      link: "/letter",
      messages: {
        yourLetter: ["Hello", "How are you?"],
        otherLetter: ["Hi", "I am fine. How are you?"],
      },
      imageURL:
        "https://images.pexels.com/photos/1043471/pexels-photo-1043471.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2",
    },

    {
      id: 4,
      userName: "Nagato",
      status: true,
      link: "/letter",
      messages: {
        yourLetter: ["Hello", "How are you?"],
        otherLetter: ["Hi", "I am fine. How are you?"],
      },
      imageURL:
        "https://images.pexels.com/photos/7654096/pexels-photo-7654096.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260",
    },
  ]);

  const {data:rooms} = useFetch(`${mainurl}/chat/get-chat-rooms/`)
  const [userChatRoom, setUserChatRoom] = useState(null)
  useEffect(()=>{
    if (rooms){
      setUserChatRoom(rooms)
    }
  },[rooms,setUserChatRoom])

  return (
    <>
    {
      userChatRoom ?
        (<ChatContext.Provider value={[chatList, userChatRoom,setChatList]}>
          {children}
        </ChatContext.Provider>)
        :
        (<Loading/>)
    }
    </>
  );
};
