import React, { useState, useEffect } from "react";
import LetterPopup from "./popup-letter.component";
import { mainurl } from "../url";


const Chat = ({ chatData }) => {
  const [letterPopup, setLetterPopup] = useState(false);
  
  return (
    <>
      <LetterPopup letterPopup={letterPopup} setLetterPopup={setLetterPopup} chatData={chatData}/>

      <div className="chat" onClick={() => setLetterPopup(true)}>
        <div className="userImage">
          <img src={`${mainurl}/media/${chatData.online[0].profile_picture}`} alt="user" />
        </div>

        <div className="other">
          <div className="userName">
            <h3>{chatData.online[0].full_name}</h3>
          </div>

          <div className="lastMessage">
            {/* {
              chatData.messages.otherLetter[
                chatData.messages.otherLetter.length - 1
              ]
            } */}
            {chatData.last_message}
          </div>
        </div>
      </div>
    </>
  );
};

export default Chat;
