import React, { useState } from "react";
import { Link, useHistory } from "react-router-dom";

import CustomInput from "../../components/custom-input.component";
import LoginSignUp from "../../components/login-sign-up.component";
import { url } from "../../url";

const EmailVerificationPage = () => {
  const history = useHistory();
  const [formValues, setFormValuse] = useState({
    userEmail: "",
  });

  const [wrongData, setWrongData] = useState(null);

  const handleChange = (event) => {
    const { name, value } = event.target;

    setFormValuse({ ...formValues, [name]: value });
  };

  const sendverfication = (e) => {
    e.preventDefault();
    fetch(url + "/email_verification/", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },

      body: JSON.stringify({
        email: formValues.userEmail,
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        if (!data.error) {
          history.push({
            pathname: "/verification-code",
            state: { email: formValues.userEmail },
          });
        } else {
          setWrongData(true);
          return 0;
        }
      })
      .catch((err) => setWrongData(true));
  };
  return (
    <LoginSignUp pageName="Verify Your Email">
      <form action="" onSubmit={sendverfication}>
        <CustomInput
          label="Email*"
          type="email"
          name="userEmail"
          handlechange={handleChange}
          value={formValues.userEmail}
        />
        {wrongData && <button>Enter a valid email.</button>}
        <button className="submit-btn">Send Verification Code</button>
      </form>

      <div className="switch-login-signup">
        New To Sanam Delivery? <Link to="/login">Login</Link>
      </div>
    </LoginSignUp>
  );
};

export default EmailVerificationPage;
