import React from "react";
import {
  ResponsiveContainer,
  Tooltip,
  BarChart,
  XAxis,
  YAxis,
  Legend,
  CartesianGrid,
  Bar,
} from "recharts";

import PropTypes from "prop-types";

const getPath = (x, y, width, height) => `M${x},${y + height}
          C${x + width / 3},${y + height} ${x + width / 2},${y + height / 3} ${
  x + width / 2
}, ${y}
          C${x + width / 2},${y + height / 3} ${x + (2 * width) / 3},${
  y + height
} ${x + width}, ${y + height}
          Z`;

const TriangleBar = (props) => {
  const { fill, x, y, width, height } = props;

  return <path d={getPath(x, y, width, height)} stroke="none" fill={fill} />;
};

TriangleBar.propTypes = {
  fill: PropTypes.string,
  x: PropTypes.number,
  y: PropTypes.number,
  width: PropTypes.number,
  height: PropTypes.number,
};

const BarChartComponent = ({ data }) => {
  return (
    <div className="bar-chart-component">
      <h2 className="title">Bar Chart</h2>
      <ResponsiveContainer width="99%" aspect={2.5}>
        <BarChart
          height={400}
          data={data}
          margin={{
            top: 5,
            right: 5,
            left: 5,
            bottom: 5,
          }}
          barSize={15}
        >
          <XAxis
            dataKey="name"
            scale="point"
            padding={{ left: 30, right: 30 }}
          />
          <YAxis />
          <Tooltip />
          <Legend />
          <CartesianGrid strokeDasharray="4 4" />
          <Bar
            dataKey="done"
            fill="#00C49F"
            shape={<TriangleBar />}
            label={{ position: "top" }}
          />
          <Bar
            dataKey="notDone"
            fill="#FF8042"
            shape={<TriangleBar />}
            label={{ position: "top" }}
          />
          <Bar
            dataKey="total"
            fill="#0088FE"
            shape={<TriangleBar />}
            label={{ position: "top" }}
          />
        </BarChart>
      </ResponsiveContainer>
    </div>
  );
};

export default BarChartComponent;
