import React, { useEffect, useState } from "react";
import Header from "./header.component";
import SideNav from "./side-nav.component";
import useFetch from "../useFetch";
import { url } from "../url";

const Layout = ({
  children,
  activeSideNav,
  activateSideNav,
  hideSideNav,
  showSideNav,
  setVendorProfile,
  vendorProfile

}) => {

  const {data:profile} = useFetch( `${url}/retrive-vendor/`)
  useEffect(()=>{
    if (profile){
      setVendorProfile(profile)
    }
  },[profile])
  return (
    <div className={`layout ${activeSideNav ? "active" : ""} `}>
      <div className="header-line"></div>
      <div className="side-nav-space">
        <SideNav
          activeSideNav={activeSideNav}
          hideSideNav={hideSideNav}
          showSideNav={showSideNav}
        />
      </div>

      <div className="all-content">
        {
          vendorProfile &&
        <Header
          activateSideNav={activateSideNav}
          activeSideNav={activeSideNav}
          hideSideNav={hideSideNav}
          showSideNav={showSideNav}
          profile= {vendorProfile.profile_picture}
        />
        }

        <div className="inner-content">{children}</div>
      </div>

      <div className="white-space"></div>
    </div>
  );
};

export default Layout;
