import React, { useState, createContext, useEffect } from "react";
import { useHistory } from "react-router-dom";
import Loading from "../components/loading.component";
import { url } from "../url";

export const OrderContext = createContext();

export const OrderProvider = ({ children, chagesDected, searchField }) => {
  const [tableAllData, setTableAllData] = useState(null);

  const history = useHistory();

  useEffect(() => {
    if (!localStorage.getItem("token")) {
      history.push("/");
    }

    fetch(
      url +
        `/filter-order-list/?${new URLSearchParams({
          query: searchField,
        })}`,
      {
        headers: {
          Accept: "application/json",
          Authorization: "Token " + localStorage.getItem("token"),
        },
      }
    )
      .then((res) => {
        if (res.status === 401) {
          localStorage.removeItem("token");
          history.push("/login");
          window.location.reload();
          throw Error("Something went wrong.Could not fetch data.");
        }

        return res.json();
      })
      .then((tableDatas) => {
        if (tableDatas !== null) {
          setTableAllData(tableDatas);
        } else {
          setTableAllData(null);
        }
      });
  }, [chagesDected, searchField]);

  return (
    <>
      {tableAllData ? (
        <OrderContext.Provider value={[tableAllData, setTableAllData]}>
          {children}
        </OrderContext.Provider>
      ) : (
        <Loading />
      )}
    </>
  );
};
