import React, { useState, useEffect } from "react";
import { Link, useHistory ,useLocation} from "react-router-dom";

import CustomInput from "../../components/custom-input.component";
import LoginSignUp from "../../components/login-sign-up.component";
import { url } from "../../url";

const VerificationCodeForget = () => {
  const [formValues, setFormValuse] = useState({
    VerificationCodeNo: "",
  });
  const location = useLocation();
  let email;
  try {
    email = location.state.email
  }
  catch(err){
    email = null
  }
  console.log(email)
  const history = useHistory();

  const handleChange = (event) => {
    const { name, value } = event.target;

    setFormValuse({ ...formValues, [name]: value });
  };

  const [wrongData, setWrongData] = useState(null);

  useEffect(() => {
    // count down
    const timer = document.querySelector(".timer");
    const resendBtn = document.querySelector("#resendBtn");

    if (timer) {
      let time = parseInt(timer.getAttribute("time"));

      const startCountdown = () => {
        time--;

        timer.innerHTML = ` AFTER ${time}S`;

        if (time === 0) {
          timer.style.display = "none";
          resendBtn.removeAttribute("disabled");
        }
      };

      setInterval(startCountdown, 1000);
    }
  }, []);

  const confirmverfication = (e) => {
    e.preventDefault();
    fetch(url + "/forget_code_verification/", {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },

      body: JSON.stringify({
        code: formValues.VerificationCodeNo,
        email: email
      }),
    })
      .then((res) => {
        if (!res.ok) {
          return res.text().then(text => {
            throw new Error(text)
          });
        }
        return res.json() 
      })
      .then((data) => {
          history.push({ 
            pathname: '/forget-password',
            state: {email: email}
           })
      })
      .catch((err) => setWrongData(true));
  }

  return (
    <LoginSignUp pageName="Verify Your Email">
      {wrongData && <p style={{color:"red"}}>Enter a valid verification code</p>}
      <form action="" onSubmit={confirmverfication}>
        <CustomInput
          label="Verification Code*"
          type="text"
          name="VerificationCodeNo"
          handleChange={handleChange}
          value={formValues.VerificationCodeNo}
        />

        <button className="submit-btn">Verify</button>
      </form>

      <div className="switch-login-signup">
        New To Sanam Delivery? <Link to="/login">Login</Link>
      </div>
    </LoginSignUp>
  );
};

export default VerificationCodeForget;
