import React, { useEffect, useState, useContext } from "react";
import { useHistory } from "react-router";
import "./assets/styles/styles.sass";
import "./assets/styles/responsive.sass";
import { Switch, Route } from "react-router-dom";

import { OrderProvider } from "./context/order.context";
import { TokenContext } from "./context/token.context";

import LoginPage from "./pages/login-page.component";
import EmailVerificationPage from "./pages/sign-up-page/email-verification-page.component";
import EmailVerificationForgetPage from "./pages/forget-password/email-verification-page.component";
import VerificationCode from "./pages/sign-up-page/verification-code.component";
import VerificationCodeForget from "./pages/forget-password/verification-code.component";
import SignUpPage from "./pages/sign-up-page/sign-up-page.component";
import Layout from "./components/layout.component";
import DashboardPage from "./pages/dashboard-page.component";
import PickupRequestPage from "./pages/pickup-request-page.component";
import TokenContextProvider from "./context/token.context";
import OrderPage from "./pages/order-page.component";
import ProfilePage from "./pages/profile-page.component";
import TotalOrdersPage from "./pages/total-orders-page.component";
import DeliveredOrdersPage from "./pages/delivered-orders-page.component";
import CashCollectionPage from "./pages/cash-collection-page.component";
import PendingOrdersPage from "./pages/pending-orders-page.component";
import CancelledOrdersPage from "./pages/cancelled-orders-page.component";
import RescheduledOrdersPage from "./pages/rescheduled-orders-page.component";
import ForgetPasswordPage from "./pages/forget-password/forget-password-page.component";
import ChangePasswordPage from "./pages/change-password-page.component";
import CreateOrderPage from "./pages/create-order-page.component";
import UpdateOrderPage from "./pages/update-order-page";
import ContactPage from "./pages/contact-page.component";
import ProblematicOrderPage from "./pages/problematic-order-page.component";
import DeliveredWithAdjustmentOrderPage from "./pages/delivered-with-adjustment-order-page.component";
import NotificationPage from "./pages/notification-page.component";
import { BankProvider } from "./context/bank-table.context";
import PopupMessage from "./components/popup-message.component";
import ChatPage from "./pages/chat-page.component";
import { ChatProvider } from "./context/chat.context";
import LetterPage from "./components/popup-letter.component";

const App = () => {
  const { token } = useContext(TokenContext);
  const [searchField, setSearchField] = useState("");

  const [activeSideNav, setActiveSideNav] = useState(true);

  const [hideSideNav, setHideSideNav] = useState(false);

  const [passwordEye, setPasswordEye] = useState(false);

  const [orderDay, setOrderDay] = useState(1);
  const [vendorProfile, setVendorProfile] = useState();

  const [chagesDected, setChagesDected] = useState(true);

  const [popupMessage, setPopupMessage] = useState("");

  const history = useHistory();

  const dectedChanges = (e) => {
    if (!e) {
      setChagesDected(true);
    } else {
      setChagesDected(false);
    }
  };

  useEffect(() => {
    // button click animation
    const button = document.querySelectorAll("button");

    button.forEach((btn) => {
      btn.addEventListener("mousedown", () => {
        btn.classList.add("animate");
      });

      btn.addEventListener("mouseup", () => {
        btn.classList.remove("animate");
      });

      btn.addEventListener("mouseout", () => {
        btn.classList.remove("animate");
      });
    });
  }, []);

  const activateSideNav = () => {
    if (!activeSideNav) {
      setActiveSideNav(true);
    } else {
      setActiveSideNav(false);
    }
  };

  const showSideNav = () => {
    if (!hideSideNav) {
      setHideSideNav(true);
    } else {
      setHideSideNav(false);
    }
  };

  const checkEyes = (e) => {
    if (!e) {
      setPasswordEye(true);
    } else {
      setPasswordEye(false);
    }
  };

  useEffect(() => {
    if (popupMessage !== "") {
      setTimeout(() => {
        setPopupMessage("");
      }, 3000);
    }
  }, [popupMessage]);

  return (
    <div className="App">
      <PopupMessage popupMessage={popupMessage} icon>
        {popupMessage}
      </PopupMessage>

      <Switch>
        <Route exact path="/login">
          <TokenContextProvider>
            <LoginPage
              token={token}
              checkEyes={checkEyes}
              passwordEye={passwordEye}
            />
          </TokenContextProvider>
        </Route>

        <Route
          exact
          path="/email-verification"
          component={EmailVerificationPage}
        />

        <Route exact path="/verification-code" component={VerificationCode} />

        <Route
          exact
          path="/email-verification-forget"
          component={EmailVerificationForgetPage}
        />

        <Route
          exact
          path="/verification-code-forget"
          component={VerificationCodeForget}
        />

        <Route exact path="/sign-up">
          <SignUpPage checkEyes={checkEyes} passwordEye={passwordEye} setPopupMessage={setPopupMessage} />
        </Route>

        <Route
          exact
          path="/forget-password"
          component={ForgetPasswordPage}
          checkEyes={checkEyes}
          passwordEye={passwordEye}
        />

        <Route exact path="/change-password">
          <ChangePasswordPage checkEyes={checkEyes} passwordEye={passwordEye} />
        </Route>

        <Layout
          activeSideNav={activeSideNav}
          activateSideNav={activateSideNav}
          hideSideNav={hideSideNav}
          showSideNav={showSideNav}
          vendorProfile={vendorProfile}
          setVendorProfile={setVendorProfile}
        >
          <Route exact path="/" component={DashboardPage} />

          <Route exact path="/orders">
            <OrderProvider
              chagesDected={chagesDected}
              searchField={searchField}
            >
              <OrderPage
                chagesDected={chagesDected}
                setChagesDected={dectedChanges}
                searchField={searchField}
                setSearchField={setSearchField}
                setPopupMessage={setPopupMessage}
              />
            </OrderProvider>
          </Route>

          <Route exact path="/pick-up-request">
            <OrderProvider
              chagesDected={chagesDected}
              searchField={searchField}
            >
              <PickupRequestPage
                chagesDected={chagesDected}
                setChagesDected={dectedChanges}
                searchField={searchField}
                setSearchField={setSearchField}
                setOrderDay={setOrderDay}
                setPopupMessage={setPopupMessage}
              />
            </OrderProvider>
          </Route>

          <Route exact path="/profile">
            <BankProvider chagesDected={chagesDected}>
              <ProfilePage
                setVendorProfile={setVendorProfile}
                chagesDected={chagesDected}
                setChagesDected={dectedChanges}
                setPopupMessage={setPopupMessage}
              ></ProfilePage>
            </BankProvider>
          </Route>

          <Route exact path="/total-orders">
            <OrderProvider searchField={searchField}>
              <TotalOrdersPage
                searchField={searchField}
                setSearchField={setSearchField}
              />
            </OrderProvider>
          </Route>

          <Route exact path="/cash-collection">
            <CashCollectionPage />
          </Route>

          <Route exact path="/pending-orders">
            <OrderProvider searchField={searchField}>
              <PendingOrdersPage
                searchField={searchField}
                setSearchField={setSearchField}
              />
            </OrderProvider>
          </Route>

          <Route exact path="/cancelled-orders">
            <OrderProvider searchField={searchField}>
              <CancelledOrdersPage
                searchField={searchField}
                setSearchField={setSearchField}
              />
            </OrderProvider>
          </Route>

          <Route exact path="/delivered-orders">
            <OrderProvider searchField={searchField}>
              <DeliveredOrdersPage
                searchField={searchField}
                setSearchField={setSearchField}
              />
            </OrderProvider>
          </Route>

          <Route exact path="/rescheduled-orders">
            <OrderProvider searchField={searchField}>
              <RescheduledOrdersPage
                searchField={searchField}
                setSearchField={setSearchField}
              />
            </OrderProvider>
          </Route>

          <Route exact path="/problematic-orders">
            <OrderProvider searchField={searchField}>
              <ProblematicOrderPage
                searchField={searchField}
                setSearchField={setSearchField}
              />
            </OrderProvider>
          </Route>

          <Route exact path="/orders-delivered-with-adjustments">
            <OrderProvider searchField={searchField}>
              <DeliveredWithAdjustmentOrderPage
                searchField={searchField}
                setSearchField={setSearchField}
              />
            </OrderProvider>
          </Route>

          <Route exact path="/create-order">
            <CreateOrderPage
              orderDay={orderDay}
              setPopupMessage={setPopupMessage}
            />
          </Route>

          <Route exact path="/update-order/:id">
            <UpdateOrderPage setPopupMessage={setPopupMessage} />
          </Route>

          <Route exact path="/contact" component={ContactPage} />

          <Route exact path="/notifications" component={NotificationPage} />

          <Route exact path="/chat">
            <ChatProvider>
              <ChatPage />
            </ChatProvider>
          </Route>

          <Route exact path="/letter">
            <ChatProvider>
              <LetterPage />
            </ChatProvider>
          </Route>
        </Layout>
      </Switch>
    </div>
  );
};

export default App;
