import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import BarChartComponent from "../components/bar-chart.component";

import DashboardCard from "../components/dashboard-card.component";
import {
  BoxIcon,
  BoxCheckedIcon,
  CashIcon,
  CancleIcon,
  EventsIcon,
  WarningIcon,
  TimeIcon,
  AlertIcon,
  TruckIcon,
  PickUpIcon,
} from "../components/icons.component";
import LineChartComponent from "../components/line-chart.component";
import PieChartComponent from "../components/pie-chart.component";
import { DASHBOARD_DATA } from "../data/dashboard.data";
import { url } from "../url";
import useFetch from "../useFetch";

const DashboardPage = () => {

  const {
    data: dashboardDetail,
    isPending,
    error,
  } = useFetch(`${url}/dashboard-details/`);
  const [dash, setDash] = useState({
    totalOrder: 0,
    problematicOrder: 0,
    pendingOrder: 0,
    deliveredOrder: 0,
    cashCollection: 0.0,
    cancelled_order: 0,
    deliveryWithAdjustment: 0,
  });

  useEffect(() => {
    if (dashboardDetail) {
      setDash((previousInputs) => ({
        ...previousInputs,
        totalOrder: dashboardDetail["total_order"],
      }));
      setDash((previousInputs) => ({
        ...previousInputs,
        problematicOrder: dashboardDetail["problematic_order"],
      }));
      setDash((previousInputs) => ({
        ...previousInputs,
        pendingOrder: dashboardDetail["pending_order"],
      }));
      setDash((previousInputs) => ({
        ...previousInputs,
        deliveredOrder: dashboardDetail["delivered_order"],
      }));
      setDash((previousInputs) => ({
        ...previousInputs,
        cashCollection: dashboardDetail["cash_collection"],
      }));
      setDash((previousInputs) => ({
        ...previousInputs,
        cancelled_order: dashboardDetail["cancelled_order"],
      }));
      setDash((previousInputs) => ({
        ...previousInputs,
        deliveryWithAdjustment: dashboardDetail["delivered_with_adjustments"],
      }));
    }
  }, [dashboardDetail]);

  useEffect(() => {
    window.scroll(0, 0);
  }, []);

  return (
    <div className="dashboard">
      <h2 className="title">Dashboard</h2>

      <div className="list">
        <Link to="/total-orders" style={{ backgroundColor: "#DFF4F8" }}>
          <DashboardCard
            name="Total Order"
            total={dash.totalOrder}
            backgroundColor="#DFF4F8"
          >
            <BoxIcon />
          </DashboardCard>
        </Link>

        <Link to="/delivered-orders" style={{ backgroundColor: "#B0FCB0" }}>
          <DashboardCard
            name="Delivered Orders"
            total={`${dash.deliveredOrder}/${dash.totalOrder}`}
            backgroundColor="#B0FCB0"
          >
            <BoxCheckedIcon />
          </DashboardCard>
        </Link>

        <Link to="/cash-collection" style={{ backgroundColor: "#DFF8DF" }}>
          <DashboardCard
            name="Cash Collection"
            total={`${dash.cashCollection}`}
            backgroundColor="#DFF8DF"
          >
            <CashIcon />
          </DashboardCard>
        </Link>

        <Link to="/pending-orders" style={{ backgroundColor: "#F8F6DF" }}>
          <DashboardCard
            name="Pending Orders"
            total={dash.pendingOrder}
            backgroundColor="#F8F6DF"
          >
            <TimeIcon />
          </DashboardCard>
        </Link>

        <Link to="/cancelled-orders" style={{ backgroundColor: "#FFA8A8" }}>
          <DashboardCard
            name="Cancelled Orders"
            total={dash.cancelled_order}
            backgroundColor="#FFA8A8"
          >
            <CancleIcon />
          </DashboardCard>
        </Link>

        <Link to="/rescheduled-orders" style={{ backgroundColor: "#F8F6DF" }}>
          <DashboardCard
            name="Rescheduled Orders"
            total="0"
            backgroundColor="#F8F6DF"
          >
            <EventsIcon />
          </DashboardCard>
        </Link>

        <Link to="/problematic-orders" style={{ backgroundColor: "#F8DFF4" }}>
          <DashboardCard
            name="Problematic Orders"
            total={dash.problematicOrder}
            backgroundColor="#F8DFF4"
          >
            <WarningIcon />
          </DashboardCard>
        </Link>

        <Link to="/emergency-alter" style={{ backgroundColor: "#FFA8A8" }}>
          <DashboardCard
            name="Emergency Alert"
            total="0"
            backgroundColor="#FFA8A8"
          >
            <AlertIcon />
          </DashboardCard>
        </Link>

        <Link to="/courier" style={{ backgroundColor: "#DFF4F8" }}>
          <DashboardCard name="Courier" total="0" backgroundColor="#DFF4F8">
            <CashIcon />
          </DashboardCard>
        </Link>

        <Link
          to="/orders-delivered-with-adjustments"
          style={{ backgroundColor: "#DFF8DF" }}
        >
          <DashboardCard
            name="Orders Delivered with Adjustments"
            total={dash.deliveryWithAdjustment}
            backgroundColor="#DFF8DF"
          >
            <TruckIcon />
          </DashboardCard>
        </Link>

        <Link to="/pick-up-order" style={{ backgroundColor: "#DFF4F8" }}>
          <DashboardCard
            name="Pick Up Orders"
            total="0"
            backgroundColor="#DFF4F8"
          >
            <PickUpIcon />
          </DashboardCard>
        </Link>
      </div>
      <LineChartComponent data={DASHBOARD_DATA} />

      <PieChartComponent data={DASHBOARD_DATA} />

      <BarChartComponent data={DASHBOARD_DATA} />
    </div>
  );
};

export default DashboardPage;
