import React from "react";
import {
  ResponsiveContainer,
  LineChart,
  Line,
  Legend,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
} from "recharts";

const LineChartComponent = ({ data }) => {
  return (
    <div className="line-charts">
      <h2 className="title">Line Chart</h2>
      <ResponsiveContainer width="99%" aspect={2.5}>
        <LineChart
          data={data}
          height={300}
          margin={{ top: 5, right: 5, left: 5, bottom: 5 }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="name" interval={"preserveStartEnd"} />
          <YAxis />
          <Tooltip />
          <Legend />
          <Line dataKey="done" stroke="#00C49F" activeDot={{ r: 8 }} />

          <Line dataKey="notDone" stroke="#FF8042" activeDot={{ r: 8 }} />

          <Line dataKey="total" stroke="#0088FE" activeDot={{ r: 8 }} />
        </LineChart>
      </ResponsiveContainer>
    </div>
  );
};

export default LineChartComponent;
