import React, { useState, useEffect } from "react";
import { Link, useHistory } from "react-router-dom";
import CustomInput from "../../components/custom-input.component";
import LoginSignUp from "../../components/login-sign-up.component";

import { url } from "../../url";

const SignUpPage = ({ checkEyes, passwordEye,setPopupMessage }) => {
  const [formValues, setFormValuse] = useState({
    email: "",
    password: "",
    confirmPassword: "",
    fullName: "",
    gender: "",
    DOB: "",
    pAddress: "",
    tAddress: "",
    mobileNo: "",
    phoneNo: "",
    panNumber: "",
    companyName: "",
    userImageUpload: "",
  });
  const [image, setImage] = useState(null);

  const [wrongData, setWrongData] = useState(null);
  const [isMatched, setIsMatched] = useState("empty");
  const [done, setDone] = useState(false);
  const history = useHistory();

  const handlechange = (event) => {
    const { name, value } = event.target;

    setFormValuse({ ...formValues, [name]: value });
  };

  const [errorEmail, setErrorEmail] = useState(null);
  const [errorContactNumber, setErrorContactNumber] = useState(null);
  const [errorPanNumber, setErrorPanNumber] = useState(null);

  const [errorMessage, setErrorMessage] = useState([null]);

  const validate = (e) => {
    e.preventDefault();
    if (formValues.email === "" || formValues.fullName === "" || formValues.gender === "" || formValues.DOB === "" || formValues.mobileNo === "" || formValues.tAddress === "" || formValues.pAddress){
      setErrorMessage("Please fill required fields")
      return false;
    }
    const test = {
      email: formValues.email,
      password: formValues.password,
      confirmPassword: formValues.confirmPassword,
      full_name: formValues.fullName,
      gender: formValues.gender,
      DOB: formValues.DOB,
      permanent_address: formValues.pAddress,
      temporary_address: formValues.tAddress,
      contact_number: formValues.mobileNo,
      contact_number_2: formValues.phoneNo,
    };

    var formData = new FormData();
    formData.append("company_name", formValues.companyName);
    formData.append("pan_number", formValues.panNumber);
    formData.append("user", JSON.stringify(test));
    if (image){
      formData.append("profile_picture", image, image.name);
    }
    // console.log("hello")
    if (!done) {
      setErrorMessage("Password Doesnot matches!!")
      return false;
    }

    fetch(url + "/create-vendor/", {
      method: "POST",
      headers: {
        Authorization: 'Token ' + localStorage.getItem('token'),
      },
      body: formData,
    })
      .then((res) => {
        if (!res.ok) {
          return res.text().then((text) => {
            throw new Error(text);
          });
        }
        return res.json();
      })
      .then((data) => {
        setWrongData(false);
        history.push({
          pathname: "/email-verification",
        });
      })
      .catch((err) => {
        const errors = JSON.parse(err.message);

        if (errors.user.email !== undefined) {
          const userError = errors.user.email[0];

          setErrorEmail(userError);
          setErrorMessage(userError);
        }

        if (errors.user.contact_number !== undefined) {
          const userError = errors.user.contact_number[0];

          setErrorContactNumber(userError);
          setErrorMessage(userError);
        }

        if (errors.user.pan_number !== undefined) {
          const userError = errors.user.pan_number[0];

          setErrorPanNumber(userError);
          setErrorMessage(userError);
        }

        setWrongData(true);
      });
  };

  useEffect(() => {
    // wizard form
    const inputGroups = document.querySelectorAll(".input-groups");
    const innerGroup = document.querySelector(".inner-group");
    const nextBtn = document.querySelectorAll("#next-btn");
    const prevBtn = document.querySelectorAll("#prev-btn");

    let dividedValue = [];
    let listOfValue = [];
    let lastValue = 0;

    inputGroups.forEach((inputGroup, idx) => {
      innerGroup.style.width = `${idx + 1}00%`;

      let length = idx + 1;
      let divide = 100 / length;

      listOfValue[idx] = divide;
    });

    lastValue = listOfValue.slice(-1).pop();

    dividedValue[0] = 0;

    for (let i = 0; i < inputGroups.length; i++) {
      dividedValue[i + 1] = dividedValue[i] + lastValue;

      let target = inputGroups[i].getAttribute("id");

      const removeAttribute = () => {
        inputGroups[i].setAttribute("id", "");

        setErrorEmail(null);
        setErrorContactNumber(null);
        setErrorPanNumber(null);
      };

      if (target === "errorOccered") {
        innerGroup.style.transform = `translateX(-${dividedValue[i]}%)`;

        setTimeout(removeAttribute, 3000);
      }
    }

    nextBtn.forEach((nBtn, idx) => {
      let newIdx = idx + 1;
      nBtn.addEventListener("click", (e) => {
        e.preventDefault();
        innerGroup.style.transform = `translateX(-${dividedValue[newIdx]}%)`;
      });
    });

    prevBtn.forEach((pBtn, idx) => {
      pBtn.addEventListener("click", (e) => {
        e.preventDefault();
        innerGroup.style.transform = `translateX(-${dividedValue[idx]}%)`;
      });
    });

    if (formValues.password === "" || formValues.confirmPassword === "") {
      setIsMatched("empty");
      setDone(false);
    }

    if (isMatched === "exist") {
      return 0;
    }

    if (
      formValues.password.length < 8 ||
      formValues.confirmPassword.length < 8
    ) {
      setIsMatched("short");
      setDone(false);
    } else if (
      formValues.password === formValues.confirmPassword &&
      formValues.password.length >= 8 &&
      formValues.confirmPassword.length >= 8
    ) {
      setIsMatched("done");
      setDone(true);
    } else {
      setIsMatched("mistake");
      setDone(false);
    }
  }, [formValues, isMatched, errorEmail, errorContactNumber]);

  useEffect(() => {
    // uploadImage
    const uploadImageBtn = document.querySelector("#uploadImageBtn");
    const uploadImage = document.querySelector(".uploadImage");
    const imageShow = document.querySelector("#imageShow");
    const fileName = document.querySelector("#fileName");
    const imageLabel = uploadImageBtn.querySelector(".form-input-label");

    if (uploadImageBtn) {
      uploadImageBtn.addEventListener("click", () => {
        uploadImage.click();
      });

      uploadImage.addEventListener("change", (e) => {
        let file = e.target.files[0];
        fileName.innerHTML = file.name;
        fileName.classList.add("active");
        setImage(e.target.files[0]);

        if (e.target.files) {
          imageLabel.classList.add("shrink");
        } else {
          imageLabel.classList.remove("shrink");
        }

        let fileReader = new FileReader();

        fileReader.onload = () => {
          let fileUrl = fileReader.result;

          imageShow.classList.add("active");

          imageShow.innerHTML = `<img src="${fileUrl}" alt="">`;
        };

        fileReader.readAsDataURL(file);
      });
    }
  }, []);

  return (
    <LoginSignUp pageName="Sign Up">
      <form action="" onSubmit={validate}>
        <div className="input-groups-section">
          {wrongData ? (
            <p style={{ color: "red", marginBottom: 10 }}>{errorMessage}</p>
          ) : null}
          <div className="inner-group">
            <div
              className="input-groups"
              id={`${errorEmail ? "errorOccered" : ""}`}
            >
              <CustomInput
                label="Email*"
                type="email"
                name="email"
                handlechange={handlechange}
                value={formValues.email}
                errorMessage={errorEmail ? errorEmail : null}
              />
              <button className="submit-btn" id="next-btn">
                Next
              </button>
            </div>

            <div className="input-groups">
              <CustomInput
                label="Password*"
                type={passwordEye ? "text" : "password"}
                name="password"
                handlechange={handlechange}
                value={formValues.password}
                checkEyes={checkEyes}
                passwordEye={passwordEye}
                password
              />

              <CustomInput
                label="Confirm Password*"
                type={passwordEye ? "text" : "password"}
                name="confirmPassword"
                handlechange={handlechange}
                value={formValues.confirmPassword}
                checkEyes={checkEyes}
                passwordEye={passwordEye}
                password
              />
              {isMatched === "empty" ? (
                <p
                  style={{
                    color: "black",
                    marginBottom: 6,
                  }}
                >
                  *Password must be atleast 8 characters!
                </p>
              ) : isMatched === "mistake" ? (
                <p
                  style={{
                    color: "red",
                    marginBottom: 6,
                  }}
                >
                  Password didn't Match!
                </p>
              ) : isMatched === "short" ? (
                <p
                  style={{
                    color: "black",
                    marginBottom: 6,
                  }}
                >
                  *Password must be atleast 8 characters!
                </p>
              ) : isMatched === "exist" ? (
                <p
                  style={{
                    color: "red",
                    marginBottom: 6,
                  }}
                >
                  Account from this phone number already exists.
                </p>
              ) : (
                <p style={{ color: "green", marginBottom: 6 }}>
                  Password Matched!
                </p>
              )}
              <div className="button-group">
                <button id="prev-btn" className="submit-btn">
                  Prev
                </button>
                <button id="next-btn" className="submit-btn">
                  Next
                </button>
              </div>
            </div>

            <div className="input-groups">
              <CustomInput
                label="First Name*"
                type="text"
                name="fullName"
                handlechange={handlechange}
                value={formValues.fullName}
              />

              <CustomInput
                label="Gender*"
                name="gender"
                handlechange={handlechange}
                value={formValues.gender}
                select
              >
                <option value="" disabled></option>
                <option value="M">Male</option>
                <option value="F">Female</option>
                <option value="O">Others</option>
              </CustomInput>

              <CustomInput
                label="Date Of Birth*"
                type="text"
                name="DOB"
                placeholder="YYYY-MM-DD"
                handlechange={handlechange}
                value={formValues.DOB}
              />

              <div className="button-group">
                <button id="prev-btn" className="submit-btn">
                  Prev
                </button>
                <button id="next-btn" className="submit-btn">
                  Next
                </button>
              </div>
            </div>

            <div className="input-groups">
              <CustomInput
                label="Upload Image"
                type="file"
                name="userImageUpload"
                handlechange={handlechange}
                value={formValues.userImageUpload}
                uploadImageBtn
                hidden
              />

              <div className="image" id="imageShow"></div>
              {/* <input type="file" onChange={fileSelectedHandler}/> */}

              <div className="button-group">
                <button id="prev-btn" className="submit-btn">
                  Prev
                </button>
                <button id="next-btn" className="submit-btn">
                  Next
                </button>
              </div>
            </div>

            <div className="input-groups">
              <CustomInput
                label="Permanent Address*"
                type="text"
                name="pAddress"
                handlechange={handlechange}
                value={formValues.pAddress}
              />

              <CustomInput
                label="Temporary Address*"
                type="text"
                name="tAddress"
                handlechange={handlechange}
                value={formValues.tAddress}
              />

              <div className="button-group">
                <button id="prev-btn" className="submit-btn">
                  Prev
                </button>
                <button id="next-btn" className="submit-btn">
                  Next
                </button>
              </div>
            </div>

            <div
              className="input-groups"
              id={`${errorContactNumber ? "errorOccered" : ""}`}
            >
              <CustomInput
                label="Mobile No.*"
                type="number"
                name="mobileNo"
                handlechange={handlechange}
                value={formValues.mobileNo}
                errorMessage={errorContactNumber ? errorContactNumber : null}
              />

              <CustomInput
                label="Phone No."
                type="number"
                name="phoneNo"
                handlechange={handlechange}
                value={formValues.phoneNo}
              />

              <div className="button-group">
                <button id="prev-btn" className="submit-btn">
                  Prev
                </button>
                <button id="next-btn" className="submit-btn">
                  Next
                </button>
              </div>
            </div>

            <div
              className="input-groups"
              id={`${errorPanNumber ? "errorOccered" : ""}`}
            >
              <h2>Company Detail</h2>
              <CustomInput
                label="Company Name*"
                type="type"
                name="companyName"
                handlechange={handlechange}
                value={formValues.companyName}
              />

              <CustomInput
                label="Pan Number"
                type="number"
                name="panNumber"
                handlechange={handlechange}
                value={formValues.panNumber}
                errorMessage={errorPanNumber ? errorPanNumber : null}
              />

              <div className="button-group">
                <button id="prev-btn" className="submit-btn">
                  Prev
                </button>
                <button className="submit-btn">Submit</button>
              </div>
            </div>
          </div>
        </div>
      </form>

      <div className="switch-login-signup">
        Already Have An Account? <Link to="/login">Login</Link>
      </div>
    </LoginSignUp>
  );
};

export default SignUpPage;
