import React, { useState, useContext, useEffect } from "react";
import { Link, useHistory } from "react-router-dom";

import CustomInput from "../components/custom-input.component";
import LoginSignUp from "../components/login-sign-up.component";
import { TokenContext } from "../context/token.context";
import { url } from "../url";

const LoginPage = ({ token, checkEyes, passwordEye }) => {
  const { changeToken } = useContext(TokenContext);
  const history = useHistory();
  const [formValues, setFormValuse] = useState({
    userEmail: "",
    password: "",
  });

  const [wrongData, setWrongData] = useState(null);

  const handleChange = (event) => {
    const { name, value } = event.target;

    setFormValuse({ ...formValues, [name]: value });
  };

  useEffect(() => {
    if (token) {
      history.push("/");
    }
  }, [token, history]);

  const validate = (e) => {
    e.preventDefault();
    fetch(url + "/login/", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },

      body: JSON.stringify({
        email: formValues.userEmail,
        password: formValues.password,
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        if (!data.error) {
          localStorage.setItem("token", data.token);
          localStorage.setItem("id", data.id);
          setWrongData(false);
          changeToken(data.token);
          // window.location.replace("/");
          history.push("/");
        } else {
          setWrongData(true);
          return 0;
        }
      })
      .catch((err) => setWrongData(true));
  };

  return (
    <LoginSignUp pageName="Login">
      <form action="" onSubmit={validate}>
        <CustomInput
          label="Email*"
          type="email"
          name="userEmail"
          handlechange={handleChange}
          value={formValues.userEmail}
        />

        <CustomInput
          label="Password*"
          type={passwordEye ? "text" : "password"}
          name="password"
          handlechange={handleChange}
          value={formValues.password}
          checkEyes={checkEyes}
          passwordEye={passwordEye}
          password
        />

        <div className="others">
          <div className="remember-me">
            <input type="checkbox" id="remember-me" />
            <label htmlFor="remember-me">Remember Me</label>
          </div>

          {wrongData === null ? (
            <div></div>
          ) : wrongData === true ? (
            <p style={{ color: "red", marginBottom: 6 }}>
              {" "}
              Email or Password is Incorrect.
            </p>
          ) : (
            <div></div>
          )}

          <div className="forget-password">
            <Link to="/email-verification-forget">Forget Password?</Link>
          </div>
        </div>

        <button className="submit-btn">Login</button>
      </form>

      <div className="switch-login-signup">
        New To Sanam Delivery? <Link to="/sign-up">Sign Up</Link>
      </div>
    </LoginSignUp>
  );
};

export default LoginPage;
